<template>
    <!-- 法币购买出售 -->
    <div class="transaction-list"> 
        <div class="transaction-c">
            <ul class="transaction-nav clr">
                <li class="nav-list" @click="handleBuy">
                    <p class="list-buy list-title" :class="{ 'active': isActive == 0 }">
                        {{$t('french.transactionList[0]')}}
                    </p>
                    <p class="price">
                        {{$t('french.transactionList[1]')}}{{ priceData.price }}CNY
                    </p>
                </li>
                <span class="desc">
                </span>
                <li class="nav-list" @click="handleSell">
                    <p class="list-sell list-title" :class="{ 'active': isActive == 1 }">
                       {{$t('french.transactionList[2]')}}
                    </p>
                    <p class="price">
                        {{$t('french.transactionList[3]')}}{{ priceData.price }}CNY
                    </p>
                </li>
                <button class="transaction-btn btn" @click="release">
                    {{ navBtnName }}
                </button>
            </ul>
            <template>
                <el-table :data="tableData" style="width: 100%;min-height: 600px" :header-cell-style="{ 'background-color': '#EBEFF3', 'height': '30px' }">
                    <el-table-column :label="$t('french.transactionList[3]')" width="240" align="center">
                        <template slot-scope="scope">
                            <div class="avatar">
                                <span class="avatar-name">
                                    {{ scope.row.realname.substr(0,1) }}
                                </span>
                            </div>
                            <div class="avatar-detail">
                                <p class="name">
                                    {{ scope.row.realname }}
                                </p>
                                <p class="turnover-rate">
                                    <span class="transaction-count">
                                        {{$t('french.transactionList[5]')}}{{ scope.row.cd_num.substring(0,scope.row.cd_num.length-2) }}
                                    </span>
                                    <span class="transaction-rate">
                                        {{$t('french.transactionList[6]')}}{{ scope.row.rate.substring(0,scope.row.rate.length-2) }}%
                                    </span>
                                </p>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column :label="$t('french.transactionList[7]')" width="130">
                        <template slot-scope="scope">
                            <span>
                                {{ scope.row.trans_num }}
                            </span>
                            <span>
                                USDT
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column :label="$t('french.transactionList[8]')" width="180">
                        <template slot-scope="scope">
                            <span>
                                {{ scope.row.quota }}
                            </span>
                            <span>
                                CNY
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column :label="$t('french.transactionList[9]')">
                        <template slot-scope="scope">
                            <span style="color: #56c182;">
                                {{ scope.row.price }}
                            </span>
                            <span style="color: #56c182;">
                                CNY
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column :label="$t('french.transactionList[10]')" align="left">
                        <template slot-scope="scope">
                            <span style="display: inline-block; width: 18px; height: 18px; border-radius: 50%; cursor: pointer;" v-if="scope.row.pay_backcard == 1">
                                <img src="../../../assets/img/frenchImg/bank_small.png" alt="" style="width: 100%;"/>
                            </span>
                            <span style="display: inline-block; width: 18px; height: 18px; border-radius: 50%; cursor: pointer;" v-if="scope.row.pay_wx == 1">
                                <img src="../../../assets/img/frenchImg/wechat_small.png" alt="" style="width: 100%;"/>
                            </span>
                            <span style="display: inline-block; width: 18px; height: 18px; border-radius: 50%; cursor: pointer;" v-if="scope.row.pay_alipay == 1">
                                <img src="../../../assets/img/frenchImg/alipay_small.png" alt="" style="width: 100%;"/>
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="address" :label="$t('french.transactionList[11]')" width="150" align="center">
                        <template slot-scope="scope">
                            <button class="handle-btn btn" @click="handleTransaction(scope.row)">
                                {{ handleBtn }}
                            </button>
                        </template>
                    </el-table-column>
                </el-table>
                <pagination :page="page" @change="pageChange"></pagination>
            </template>
            <el-dialog :title="dialogTitle" :visible.sync="dialogVisible">
                <div class="dialog-c">
                    <div class="input-warp">
                        <span class="label">
                            {{ priceLabel }}
                        </span>
                        <input 
                        type="text" 
                        class="dialog-input" 
                        v-model="form.total_price" 
                        :placeholder="pricePlaceholder" 
                        @blur="changeValue"
                        @input="calculateCount"/>
                        <span class="unit">
                            CNY
                        </span>
                    </div>
                    <div class="input-warp">
                        <span class="label">
                            {{ countLabel }}
                        </span>
                        <input 
                        type="text" 
                        class="dialog-input" 
                        v-model="form.total_num" 
                        :placeholder="countPlaceholder"
                        @blur="changeValue"
                        @input="calculatePrice"/>
                        <span class="unit">
                            USDT
                        </span>
                    </div>
                    <div class="input-warp">
                        <span class="label">
                            {{$t('french.transactionList[12]')}}
                        </span>
                        <input 
                        type="password" 
                        class="dialog-input" 
                        v-model="form.tpwd" 
                        :placeholder="$t('french.transactionList[13]')"/>
                    </div>
                    <div class="dialog-footer">
                        <button class="btn dialog-btn" @click="submitOrder">
                            {{$t('french.transactionList[14]')}}
                        </button>
                        <button class="cancel-btn dialog-btn" @click="closeDialog">
                            {{$t('french.transactionList[15]')}}
                        </button>
                    </div>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import  pagination from "@/components/pagination";
import { trading, createOrder, paymentList, getPrice } from "../../../api/c2c.js"
export default {
    name: 'transactionList',
    data() {
        return {
            page:{
                size: 10,
                p: 1,
                total: 10
            },
            form: {
                total_num: '',
                total_price: '',
                type: '',
                tpwd: '',
                order_no: '',
                account: JSON.parse(sessionStorage.getItem('account'))
            },
            type: 'sell',
            navBtnName: this.$t('french.transactionList[16]'),
            handleBtn: this.$t('french.transactionList[17]'),
            dialogTitle: this.$t('french.transactionList[18]'),
            priceLabel: this.$t('french.transactionList[19]'),
            countLabel: this.$t('french.transactionList[20]'),
            pricePlaceholder: this.$t('french.transactionList[21]'),
            countPlaceholder: this.$t('french.transactionList[22]'),
            // isActive:  0 购买  1出售,
            isActive: 0,
            dialogVisible: false,
            tableData: [],
            dialogData: '',
            paymentData: [],
            priceData: {}
        }
    },
    created() {
        // 获取初始数据
        this.getListData()
    },
    methods: {
        pageChange(row){
            this.page.p = row
            this.getListData()
        },
        // 获取初始列表数据
        getListData() {
            // 获取购买/出售列表
            trading({ 
                "type": this.type, 
                p: this.page.p, 
                size: this.page.size }).then(res=> {
                if (res.data.status == 200) {
                    this.tableData = res.data.data.res
                    this.page.total = res.data.data.count;
                }
            })
            // 获取支付方式
            paymentList().then(res=> {
                if (res.data.status == 200) {
                    this.paymentData = res.data.data
                }
            })
            // 获取参考价CNY
            getPrice().then(res=> {
                if (res.data.status == 200) {
                    this.priceData = res.data.data
                    return
                }
            })
        },
        // 输入金额失去焦点
        changeValue() {
            this.$public.throttle(()=>{
                if (this.dialogVisible = true) {
                    if (Number(this.form.total_price) > Number(this.dialogData.max_price)) {
                        this.$public.msg(this.$t('french.transactionList[23]') + this.dialogData.max_price + 'CNY', 'error', this)
                        this.form.total_price = ''
                        this.form.total_num = ''
                        return
                    } else if(Number(this.form.total_price) < Number(this.dialogData.min_price)) {
                        this.$public.msg(this.$t('french.transactionList[24]') + this.dialogData.min_price + 'CNY', 'error', this)
                        this.form.total_price = ''
                        this.form.total_num = ''
                        return
                    } else if (this.form.total_num > this.dialogData.trans_num) {
                        this.$public.msg(this.$t('french.transactionList[25]') + this.dialogData.trans_num + 'USDT', 'error', this)
                        this.form.total_num = ''
                        this.form.total_price = ''
                    }
                } 
            },2000)
        },
        // 根据金额计算数量
        calculateCount() {
            var count = this.form.total_price / this.dialogData.price
            this.form.total_num = Math.floor(count * 100) / 100
        },
        // 根据数量计算金额
        calculatePrice() {
            var totalPrice = this.form.total_num * this.dialogData.price
            this.form.total_price =  Math.floor(totalPrice * 100) / 100
        },
        // 购买/出售
        handleTransaction(rowData) {
            if (rowData.account == sessionStorage.getItem('account')) {
                this.$public.msg(this.$t('french.transactionList[49]'), 'error', this)
                return
            } else if (!sessionStorage.account || !sessionStorage.token) {
                this.$public.msg(this.$t('french.transactionList[26]'), 'error' ,this)
                this.$router.push({ name: 'login' })
                return
            } else {
                this.form.total_num = ''
                this.form.total_price = ''
                this.form.tpwd = ''
                // 购买/出售的单行数据
                this.dialogData = rowData
                // 出售时判断对方支付方式是否和自己对应
                if (this.isActive == 1) {
                    var payMethods = []
                    if (JSON.parse(sessionStorage.getItem('userInfo')).is_shop != 1) {
                        this.$public.msg(this.$t('french.transactionList[27]'), 'error' ,this)
                        return
                    } else {
                        if (this.dialogData.pay_alipay == 1) {
                            payMethods.push('alipay')
                        } 
                        if (this.dialogData.pay_backcard == 1) {
                            payMethods.push('backcard')
                        }
                        if (this.dialogData.pay_wx == 1) {
                            payMethods.push('wx')
                        }
                        for (var i=0; i<payMethods.length; i++) {
                            for (var j=0; j<this.paymentData.length; j++) {
                                if (payMethods[i] == this.paymentData[j].type) {
                                    this.dialogVisible = true
                                    return
                                }
                            }
                        }
                        if (!this.dialogVisible) {
                            this.$public.msg(this.$t('french.transactionList[28]'), 'error', this)
                        }
                    }
                } else {
                    this.dialogVisible = true
                }
            }
        },
        // 确定下单 购买/出售
        submitOrder() {
            if (this.form.total_price == '') {
                this.$public.msg(this.$t('french.transactionList[29]'),'error',this) 
                return
            } else if (this.form.total_num == '') {
                this.$public.msg(this.$t('french.transactionList[30]'),'error',this) 
                return
            } else if (this.form.tpwd == '') {
                this.$public.msg(this.$t('french.transactionList[31]'),'error',this) 
                return
            } else if (!this.$public.pwd(this.form.tpwd)) {
                this.$public.msg(this.$t('french.transactionList[32]'),'error',this)
                this.form.tpwd = ''
                return
            } else {
                // google
                this.$public.googleVerify().then((r)=> {
                    // 资金密码加密
                    this.form.tpwd = this.$public.Cryptographiccapital(this.form.tpwd)
                    this.form.order_no = this.dialogData.order_no
                    // 购买确认下单
                    if (this.isActive == 0) {
                        this.form.type = '1'
                        createOrder(this.form).then(res=> {
                            if (res.data.status == 200) {
                                this.$router.push({ name: 'buyPage', query: { order_num: res.data.data.order_num } })
                                this.dialogVisible = false
                            } else {
                                this.$public.msg(res.data.msg,'error',this)
                                return
                            }
                        })
                        // 出售确认下单
                    } else if (this.isActive == 1) {
                        this.form.type = '2'
                        createOrder(this.form).then(res=> {
                            if (res.data.status == 200) {
                                this.$router.push({ name: 'sellPage',  query: { order_num: res.data.data.order_num } })
                                this.dialogVisible = false
                            } else {
                                this.$public.msg(res.data.msg,'error',this)
                                return
                            }
                        })
                    };
                }).catch((r)=>{})
            }
        },
        // 发布购买/出售
        release() {
            if (!sessionStorage.account || !sessionStorage.token) {
                this.$public.msg(this.$t('french.transactionList[33]'), 'error' ,this)
                this.$router.push({ name: 'login' })
            } else if (JSON.parse(sessionStorage.getItem('userInfo')).is_shop != 1) {
                this.$public.msg(this.$t('french.transactionList[34]'), 'error' ,this)
                return
            } else {
                if (this.isActive == 0) {
                    this.$router.push({ name: 'releaseBuy' })
                } else if (this.isActive == 1) {
                    this.$router.push({ name: 'releaseSell' })
                }
            }
        },
        // 点击购买nav
        handleBuy() {
            this.page.p = 1
            this.type = "sell"
            this.getListData()
            this.isActive = 0
            this.navBtnName = this.$t('french.transactionList[35]')
            this.handleBtn = this.$t('french.transactionList[36]')
            this.dialogTitle = this.$t('french.transactionList[37]')
            this.priceLabel = this.$t('french.transactionList[38]')
            this.countLabel = this.$t('french.transactionList[39]')
            this.pricePlaceholder = this.$t('french.transactionList[40]')
            this.countPlaceholder = this.$t('french.transactionList[41]')
        },
        // 点击出售nav
        handleSell() {
            this.page.p = 1
            this.type = "pmma"
            this.getListData()
            this.isActive = 1
            this.navBtnName = this.$t('french.transactionList[42]')
            this.handleBtn = this.$t('french.transactionList[43]')
            this.dialogTitle = this.$t('french.transactionList[44]')
            this.priceLabel = this.$t('french.transactionList[45]')
            this.countLabel = this.$t('french.transactionList[46]')
            this.pricePlaceholder = this.$t('french.transactionList[47]')
            this.countPlaceholder = this.$t('french.transactionList[48]')
        },
        // 关闭弹窗
        closeDialog() {
            this.dialogVisible = false
        }
    },
    watch: {
        'form.total_price'(n, o) {
            var de = String(n).split(".")
            // 非数字限制
            if (isNaN(Number(n))) {
                this.form.total_price = o
            }
            // 位数限制 位数限制 限价时
            if (de.length == 2) {
                if (de[1].length > 2) {
                    this.form.total_price = o
                }
            }
        },
        'form.total_num'(n, o) {
            var de = String(n).split(".")
            // 非数字限制
            if (isNaN(Number(n))) {
                this.form.total_num = o
            }
            // 位数限制 位数限制 限价时
            if (de.length == 2) {
                if (de[1].length > 4) {
                    this.form.total_num = o
                }
            }
        }
    },
    components: {
        pagination,
    }
}
</script>
<style lang="less">
    @import '../../../assets/index.less';
    @import 'transactionList.less';
</style>



